<template>
  <el-form ref="form" :model="form" size="medium" label-width="110px" label-position="left">
    <el-form-item label="">
      <el-switch v-model="form['readonly']" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['editable']" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['clearable']" />
    </el-form-item>
    <el-form-item>
      <el-tooltip
        slot="label"
        class="item"
        effect="dark"
        content="element icon, el-icon-edit"
        placement="left"
      >
        <div><i class="el-icon-warning" /></div>
      </el-tooltip>
      <el-input v-model="form['prefix-icon']" />
    </el-form-item>
    <el-form-item label="">
      <el-tooltip
        slot="label"
        class="item"
        effect="dark"
        content="element"
        placement="left"
      >
        <div><i class="el-icon-warning" /></div>
      </el-tooltip>
      <el-input v-model="form['start-placeholder']" />
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="form['size']" placeholder="">
        <el-option v-for="item in SIZE" :key="item.value" :label="item.label" :value="item.value" />
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="form['align']" placeholder="">
        <el-option
          v-for="item in ALIGNS"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
    </el-form-item>

    <el-form-item label="">
      <el-switch v-model="form['is-range']" />
    </el-form-item>
    <template v-if="form['is-range']">
      <el-form-item label="">
        <el-input v-model="form['start-placeholder']" />
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form['end-placeholder']" />
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form['range-separator']" :maxlength="1" />
      </el-form-item>
    </template>
  </el-form>
</template>

<script>
import { SIZE, ALIGNS } from './config';

export default {
  name: 'ElTimePickerEdit',
  props: {
    form: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return { SIZE, ALIGNS };
  },
  watch: {},
  methods: {},
};
</script>
