<template>
  <el-form ref="form" :model="form" size="medium" label-width="110px" label-position="left">
    <el-form-item label="">
      <el-input-number v-model="form.max" :min="0" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['allow-half']" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['show-text']" />
    </el-form-item>

    <template v-if="form['show-text']">
      <el-form-item label="">
        <el-color-picker v-model="form['text-color']" size="mini" />
      </el-form-item>
      <el-form-item label="">
        <el-tooltip
          slot="label"
          class="item"
          effect="dark"
          content=""
          placement="left"
        >
          <div><i class="el-icon-warning" /></div>
        </el-tooltip>
        <el-select
          v-model="form.texts"
          multiple
          filterable
          allow-create
          default-first-option
          placeholder=""
        />
      </el-form-item>
    </template>
  </el-form>
</template>

<script>
import { SIZE, ALIGNS } from './config';

const DATE_TYPE = [
  {
    label: '',
    value: 'year',
  },
  {
    label: '',
    value: 'month',
  },
  {
    label: '',
    value: 'date',
  },
  {
    label: '',
    value: 'datetime',
  },
  {
    label: '',
    value: 'datetimerange',
  },
  {
    label: '',
    value: 'daterange',
  },
  {
    label: '',
    value: 'monthrange',
  },
];

const HAS_RANGE = ['datetimerange', 'daterange', 'monthrange'];

export default {
  name: 'ElRateEdit',
  props: {
    form: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return { SIZE, ALIGNS, DATE_TYPE };
  },
  computed: {
    isRange() {
      return HAS_RANGE.includes(this.form.type);
    },
  },
  watch: {},
  methods: {},
};
</script>

<style scoped>
.input-number-wrapper {
  width: 100%;
}
</style>
