<template>
  <el-form ref="form" :model="form" size="medium" label-width="110px" label-position="left">
    <el-form-item label="">
      <el-input v-model="form['no-data-text']" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['clearable']" />
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="form['filterable']" />
    </el-form-item>
    <template v-if="form.filterable">
      <el-form-item label="">
        <el-input v-model="form['no-match-text']" />
      </el-form-item>
    </template>
    <el-form-item label="">
      <el-switch v-model="form['multiple']" />
    </el-form-item>
    <template v-if="form.multiple">
      <el-form-item label="">
        <el-tooltip
          slot="label"
          class="item"
          effect="dark"
          content=""
          placement="left"
        >
          <div><i class="el-icon-warning" /></div>
        </el-tooltip>
        <el-switch v-model="form['collapse-tags']" />
      </el-form-item>
      <el-form-item label="">
        <el-tooltip
          slot="label"
          class="item"
          effect="dark"
          content=" 0 "
          placement="left"
        >
          <div><i class="el-icon-warning" /></div>
        </el-tooltip>
        <el-input-number v-model="form['multiple-limit']" :min="0" />
      </el-form-item>
    </template>

    <DataSource :select-options="selectOptions" />
  </el-form>
</template>

<script>
import DataSource from './DataSource.vue';

export default {
  name: 'ElSelectEdit',
  components: { DataSource },
  props: {
    form: {
      type: Object,
      default() {
        return {};
      },
    },
    selectOptions: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
  methods: {},
};
</script>

<style scoped>
.input-number-wrapper {
  width: 100%;
}
</style>
